import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/StartScreen.css';


export default function StartScreen() {
  const navigate = useNavigate();
  const [mode, setMode] = useState(''); //    
  const [nickname, setNickname] = useState('');
  const [error, setError] = useState('');

  const handleSignup = async () => {
    if (!nickname.trim()) {
      setError(' .');
      return;
    }

    try {
      const response = await fetch('http://localhost:3001/api/signup', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ nickname }),
      });

      if (!response.ok) {
        throw new Error('  ');
      }

      const data = await response.json();
      console.log('  :', data);
      navigate('/game'); //   
    } catch (err) {
      console.error(err);
      setError('  .');
    }
  };

  const handleLogin = async () => {
    if (!nickname.trim()) {
      setError(' .');
      return;
    }

    try {
      const response = await fetch('http://localhost:3001/api/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ nickname }),
      });

      

      if (response.status === 404) {
        alert('   .');
        return;
      }

      if (!response.ok) {
        throw new Error(' ');
      }

      navigate('/game'); //   
    } catch (err) {
      console.error(err);
      setError('    .');
    }
  };

  return (
    <div className="start-screen">
      <h1>Welcome to the Clothing Game</h1>
      <div className="button-group">
        <button onClick={() => setMode('signup')} className="signup-button"></button>
        <button onClick={() => setMode('login')} className="login-button"></button>
        <button onClick={() => navigate('/explore')} className="explore-button"></button>
      </div>

      {mode === 'signup' && (
        <div className="signup-form">
          <input
            type="text"
            placeholder=" ."
            value={nickname}
            onChange={(e) => setNickname(e.target.value)}
            className="nickname-input"
          />
          <button onClick={handleSignup} className="start-game-button">Start Game</button>
        </div>
      )}

      {mode === 'login' && (
        <div className="login-form">
          <input
            type="text"
            placeholder=" ."
            value={nickname}
            onChange={(e) => setNickname(e.target.value)}
            className="nickname-input"
          />
          <button onClick={handleLogin} className="start-game-button">Start Game</button>
        </div>
      )}

      {error && <p className="error-message">{error}</p>}
    </div>
  );
}
